import Homepage from './components/Homepage.vue';
import SinglePost from './components/SinglePost.vue';


export const routes = [
	{
		path: '',
		component: Homepage,
		name: 'Homepage'
	},
	{
		path: '/post',
		component: SinglePost,
		name: 'SinglePost'
	}
];


